import { Pipe, PipeTransform } from '@angular/core';

export enum ControlType{
    Text=0,
    //DateTime=1,
    Help=1,
    DropDown=2,
    DateRange=3,
    NumberRange=4,
    Search=5,
    Date=6,
    CheckboxGroup=7,
    DateRangeTime=8,
    Radio=9,
    Year=10,
    Month=11,
    MonthRange=12,
    YearRange=13,
    InputGroup=14,
    Number=15
}

@Pipe({name: 'panelControlType'})
export class PanelControlTypeXPipe implements PipeTransform {
    transform(value): Array<any> {
        let type;
        switch(value.control.getControlType()){
            case ControlType.Text:
                type = 'input';
                break;
            case ControlType.Search:
                type = 'search';
                break;
            case ControlType.Date:
                type = 'date';
                break;
            case ControlType.DateRange:
                type = 'daterange';
                break;
            case ControlType.DateRangeTime:
                type = 'daterangetime';
                break;
            case ControlType.Year:
                type = 'year';
                break;
            case ControlType.Month:
                type = 'month';
                break;
            case ControlType.MonthRange:
                type = 'monthrange';
                break;
            case ControlType.YearRange:
                type = 'yearrange';
                break;
            case ControlType.DropDown:
                type = 'dropdown';
                break;
            case ControlType.NumberRange:
                type = 'number';
                break;
            case ControlType.Help:
                type = 'help';
                break;
            case ControlType.CheckboxGroup:
                type = 'checkboxgroup';
                break; 
            case ControlType.Radio:
                type = 'radio';
                break;
            case ControlType.InputGroup:
                type = 'input-group';
                break;
            case ControlType.Number:
                type = 'singlenumber';
                break;
            default:
                type = 'input';
                break;     
        }
        return type;
    }
}
