export function deepCopy(obj) {
    var newobj = obj.constructor === Array ? [] : {};
    if (typeof obj !== 'object') {
      return obj;
    } else {
        for (var i in obj) {
            if (typeof obj[i] === 'object'){ //å¤æ­å¯¹è±¡çè¿æ¡å±æ§æ¯å¦ä¸ºå¯¹è±¡
                const type = Object.prototype.toString.call(obj[i]);
                if(type == '[object Null]'){
                    newobj[i] = obj[i];
                }
                else{
                    newobj[i] = deepCopy(obj[i]);  //è¥æ¯å¯¹è±¡è¿è¡åµå¥è°ç¨
                }
            }else{
                newobj[i] = obj[i];
            }
        }
    }
    return newobj; //è¿åæ·±åº¦åéåçå¯¹è±¡
}  