import { Component, Input, OnInit, OnChanges, EventEmitter, Output} from '@angular/core';

@Component({
    selector: 'panel-filter-radio',
    templateUrl: './radio.component.html'
    // encapsulation: ViewEncapsulation.None
})

export class PanelRadioComponent implements OnInit,OnChanges{
    _item:any;
    @Input()
    get item() {
        return this._item;
    }
    set item(data) {
        this._item = data;
        this.getDataSource();
        if(!this._item.hasOwnProperty('value')) {
            this._item.value ={value : ''};
        }
        //this.radioValue = this._item.value.getOriginalValue();
        this.radioValue = this._item.value.value;
    }
    @Output('searchHandle') searchHandle = new EventEmitter();
    /**å¤æ­æ¯å¦æ¯å¨æ©å±åºåå±ç¤º */
    extend:boolean = false;
    /**å¤æ­æ¯å¦æ¾ç¤ºlabel */
    showlabel:boolean = true;
    radioValue: any[];

    selectCheck = [];

    radioDataSource:Array<any>=new Array<any>();

    constructor(){}

    ngOnInit(): void {
        this.getDataSource();
    }

    ngOnChanges(): void {
        this.getDataSource();
    }

    getDataSource(){
        if(this._item && this._item.control){
            let radioboxList=(this._item.control);
            this.radioDataSource = Array<any>();

            if(radioboxList.enumValues && radioboxList.enumValues.length>0){
                radioboxList.enumValues.forEach(element => {
                    this.radioDataSource.push(Object.assign({},element));
                });
            }
        }
    }

    onSearch(event){
        if(event){
            this._item.value.value = event;
        }
        else{
            this._item.value.value = '';
        }
       // this._item.valueText = this._item.value.value;
        this.getValueText();
        //this.radioValue = this._item.value.getOriginalValue();
        this.radioValue = this._item.value.value;
        this.searchHandle.emit();
    }

    getValueText(){
        const enumValue = (this._item.control).enumValues;
        const value = (this._item.value).value;
        if(enumValue && enumValue.length){
            const texts = enumValue.find(val=>{
                return val['value'] == value;
            });
            if(texts){
                this._item.valueText = texts['name'];
            }
        }
    }
}