import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
  name: 'filterHighlightSearchResult',
})

export class FilterHighlightSearchPipe implements PipeTransform {

  public transform(value: string, args?: string): string {
    const needMatchValue = args;  // ç¨æ·å¨inputæ¡è¾å¥çå¼
    let endValue = value;  // æç»å¤çå¥½å¹¶é«äº®æ è¯çç»æ
    const isEnglishReg = new RegExp('^[a-zA-Z]+$');
    if (isEnglishReg.test(needMatchValue)) {     // è¾å¥æ¼é³çæåµ
      endValue = this.getEndMatchedValue(value, needMatchValue, value);
    } else {
      const searchValue = needMatchValue;
      const regExp = new RegExp(searchValue, 'gi'); // å¨å±å¹éå¿½ç¥å¤§å°å
      if (regExp.test(value)) {
        endValue = this.getEndMatchedValue(value, needMatchValue, value);
      }
    }
    return endValue;
  }

  public getEndMatchedValue(value: string, searchValue: string, oriValue: string): string {
    if (searchValue) {
      let endValue = oriValue;
      const upperValue = value.toUpperCase();
      const upperSearch = searchValue.toUpperCase();
      const matchIndex = upperValue.indexOf(upperSearch);
      if (matchIndex !== -1) {
        // å°å¹éå°çåå®¹è¿è¡é«äº®æ æ³¨
        endValue = oriValue.substring(0, matchIndex) +
          `<span style="color: #FF9800">${oriValue.substring(matchIndex, matchIndex + searchValue.length)}</span>` +
          oriValue.substring(matchIndex + searchValue.length);
      }
      return endValue;
    } else {
      return oriValue;
    }
  }
}