import { Component, Input, OnInit, HostBinding, EventEmitter, Output, ElementRef, ViewChild } from '@angular/core';
import { NumberSpinnerComponent } from '@farris/ui-number-spinner';


@Component({
    selector: 'panel-filter-single-number',
    templateUrl: './singlenumber.component.html'
})

export class PanelSingleNumberComponent implements OnInit {

    @Output() searchHandle = new EventEmitter();
    @Output() submitChange = new EventEmitter();
    canNull = true;
    number: string;
    precision: number;
    placeholder: string;
    bigNumber: boolean;
    max: number;
    min: number;

    _item: any;
    numberRange: string;
    @Input()
    set item(nr: any) {
        this._item = nr;
        if (this._item) {
            if (!this._item.hasOwnProperty('value')) {
                this._item.value = { value: null };
            }
            if (this._item.placeHolder) {
                this.placeholder = this._item.placeHolder || '';
            }
        }
        this.precision = nr.control.precision;
        this.bigNumber = nr.control.bigNumber;
        if (!this.bigNumber) {
            this.min = -2147483648;
            this.max = 2147483647;
        }
        this.setSubmitChange();
    }
    get item() {
        return this._item;
    }

    @ViewChild('numberText') numberText: NumberSpinnerComponent;

    constructor() { }

    ngOnInit(): void {
        if (this.numberText) {
            this.numberText.input.nativeElement.focus();
        }
    }

    numberChange(event) {
        this._item.value.value = event;
        if (Object.keys(this._item.value).length === 0) {
            this._item.valueText = '';
        } else {
            this._item.valueText = '' + this._item.value.value;
        }
        this.setSubmitChange();
        this.searchHandle.emit();
    }

    setSubmitChange() {
        const val = this._item.value;
        if (Object.keys(val).length < 0 || val.value == null) {
            this.submitChange.emit(false);
        } else {
            this.submitChange.emit(true);
        }
    }
}
